import { v4 as uuidv4 } from 'uuid'
import { store } from './constant';

export const generateNode = (
  type,
  title = '',
  content = ''
) => {
  const createUid = store.customCreateNodeId || uuidv4
  switch (type) {
    case 'normal':
      return {
        id: createUid(),
        type: 'normal',
        title,
        content,
        configData: {},
        nodeList: []
      }
    case 'condition':
      return {
        id: createUid(),
        type: 'condition',
        title: '',
        children: [
          {
            id: createUid(),
            title: '1',
            content: '1',
            type: 'normal',
            configData: {},
            nodeList: []
          },
          {
            id: createUid(),
            type: 'normal',
            title: '2',
            content: '2',
            configData: {},
            nodeList: []
          }
        ]
      }
    default:
      break
  }
}
